/* Purchase Process */
.purchase-process {
  padding: 4rem 2rem;
  text-align: center;
  background-color: #64b5f6;
  min-height: 100vh; /* Usar min-height para evitar problemas en pantallas pequeñas */
}

#purchase-process-title {
  margin-top: 10%;
}

.purchase-process h2 {
  font-size: 2.5rem; /* Tamaño más grande para pantallas grandes */
  margin-bottom: 2rem;
  color: #333;
}

.process-steps {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.step {
  flex: 1;
  max-width: 280px; /* Ajuste del ancho máximo para más consistencia */
  padding: 2rem;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  border-top: 4px solid #4d90fe;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  text-align: center;
}

.step:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.step h3 {
  font-size: 1.5rem; /* Tamaño más consistente con pantallas grandes */
  color: #333;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.step p {
  color: #666;
  font-size: 1rem;
  line-height: 1.6;
}

/* Icono de paso */
.step-icon {
  font-size: 3rem; /* Icono más grande en pantallas grandes */
  color: #4d90fe;
  margin-bottom: 1rem;
}

/* Animación de aparición */
.fade-in {
  opacity: 0;
  transform: translateY(40px); /* Mayor desplazamiento inicial para hacer la animación más visible */
  transition: opacity 0.8s ease-out, transform 0.8s ease-out; /* Animación más rápida */
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Estilos responsivos */
@media (max-width: 1024px) {
  .purchase-process h2 {
    font-size: 2.2rem;
  }

  .step {
    max-width: 45%; /* Ajuste para pantallas medianas */
  }

  .step h3 {
    font-size: 1.4rem;
  }

  .step-icon {
    font-size: 2.5rem;
  }
}

@media (max-width: 768px) {
  .process-steps {
    flex-direction: column;
    gap: 1.5rem;
  }

  .step {
    max-width: 100%;
  }

  .purchase-process h2 {
    font-size: 1.8rem;
  }

  .step h3 {
    font-size: 1.2rem;
  }

  .step-icon {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .purchase-process h2 {
    font-size: 1.6rem;
  }

  .step p {
    font-size: 0.9rem;
    line-height: 1.4;
  }

  .step-icon {
    font-size: 1.8rem;
  }
}
